<template>
    <div class="home h">
      <el-container class="h">
          <left-side @showmsg="getcomponentmsg" :msg="msg"></left-side>
          <el-main>
            <div class="breadcrumb">
              <el-breadcrumb separator="/">
                <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
                <el-breadcrumb-item><a href="/"></a></el-breadcrumb-item>
                <el-breadcrumb-item></el-breadcrumb-item>
              </el-breadcrumb>
            </div>

            <div class="content">
              <h2>{{ title }}</h2>
              <p>
                <span>{{ author }}</span>
                <span>{{ publishDate }}</span>
                <span>{{ viewCount }}</span>
              </p>
              <div class="con" v-html="content"></div>
              <!-- <div class="con mt20">
                <h4>codemirror</h4>
                <codemirror v-model="code" :options="cmOptions"></codemirror>
              </div> -->
              <div class="rate">
                <el-rate
                  v-model="rate"
                  show-text>
                </el-rate>
              </div>
              <p class="tc">
                <el-button type="warning" icon="el-icon-share">10</el-button>
                <el-button type="success" icon="el-icon-star-off" @click="showPayBox"></el-button>
              </p>
              <div class="comment-box">
                <h2></h2>
                <el-form ref="form" :model="form" label-width="80px">
                  <el-form-item label="">
                    <el-input v-model="form.name" placeholder=""></el-input>
                  </el-form-item>
                  <el-form-item label="">
                    <el-input v-model="form.email" placeholder=""></el-input>
                  </el-form-item>
                  <el-form-item label="">
                    <el-input v-model="form.url" placeholder=""></el-input>
                  </el-form-item>
                  <el-form-item label="">
                    <el-input type="textarea" v-model="form.desc" placeholder=""></el-input>
                  </el-form-item>
                  <el-form-item>
                    <el-button type="primary" @click="onSubmit"></el-button>
                  </el-form-item>
                </el-form>
              </div>
            </div>
            <foot></foot>
          </el-main>
      </el-container>
    </div>
</template>

<script>
import leftSide from '@/components/aside.vue'
import foot from '@/components/footer.vue'
// require component
import { codemirror } from 'vue-codemirror'
// language js
import 'codemirror/mode/javascript/javascript.js'
// require styles
import 'codemirror/lib/codemirror.css'
// theme css
import 'codemirror/theme/base16-dark.css'

export default {
  name: 'Single',
  data () {
    return {
      msg: '',
      id: '',
      title: '',
      author: '',
      publishDate: '',
      viewCount: '',
      content: '',
      fullscreenLoading: true,
      rate: null,
      form: {
        name: '',
        email: '',
        url: '',
        desc: ''
      },
      code: `const pluckDeep = key => obj => key.split(".").reduce((accum, key) => accum[key], obj)

const compose = (...fns) => res => fns.reduce((accum, next) => next(accum), res)

const unfold = (f, seed) => {
  const go = (f, seed, acc) => {
    const res = f(seed)
    return res ? go(f, res[1], acc.concat([res[0]])) : acc
  }
  return go(f, seed, [])
}`,
      cmOptions: {
        // codemirror options
        tabSize: 4,
        mode: 'text/javascript',
        theme: 'base16-dark',
        lineNumbers: true,
        line: true,
        readOnly: true
      }
    }
  },
  components: {
    leftSide,
    foot,
    codemirror
  },
  mounted () {
    this.fetchData()
  },
  methods: {
    async fetchData () {
      this.id = this.$route.params.id // URLid
      //   TODO
      // mock
      switch (this.id) {
        case 1:
          this.title = 'Vue'
          break
        case 2:
          this.title = 'PDF.jsPDFObject.js'
          break
        case 3:
          this.title = 'Echarts'
          break
        case 4:
          this.title = 'js demo'
          break
      }
      this.author = 'haibao'
      this.publishDate = '2018-08-13'
      this.viewCount = '200'
      this.content = '<strong>ID</strong>' + this.id
    },
    getcomponentmsg (msg) {
      alert('-----' + msg)
    },
    showPayBox () {
      // 
      this.$alert('<strong></strong>', '', {
        dangerouslyUseHTMLString: true
      })
    },
    onSubmit () {
      // TODO.
      alert('submit')
    }
  }
}
</script>

<style scoped>
.breadcrumb {
  background-color: #f2f2f2;
  padding: 10px;
}
.content {
  padding: 25px 0;
  min-height: 450px;
}
.rate, .mt20 {
  margin-top: 20px;
}
.comment-box {
  margin-top: 40px;
}
.comment-box h2 {
  margin-bottom: 20px;
}
.tc {
  text-align: center;
}
</style>



